import { MapViewState, EventState, getDefaultEventState } from "../MapViewState";
import { MapState, MapStateContext } from "./MapState";
import { ExploreMapState } from "./ExploreMapState";
import { CreateEventMapState } from "./CreateEventMapState";
import { Typography, Row, Col } from "antd";
import React from "react";
import moment from "moment";
import { Comment, Avatar, Form, Button, List, Input, Tooltip } from 'antd';
import TextArea from "antd/lib/input/TextArea";

export class ViewEventMapState extends MapState {
  eventId: null;

  render(context: MapStateContext): JSX.Element {
    const doc = context.viewState.allEvents.get(this.eventId);

    window.history.pushState(null, null, '#/events/' + this.eventId);

    const comment = "Please join me on " + moment(doc.get('planned').toDate()).format('Do of MMM') + " for a hike."
    
    return (
      <Row>
        <Col span={22} offset={1}>
          <Typography.Title level={2}>{doc.get('summary')}</Typography.Title>
        </Col>
        <Col span={20} offset={1}>
          <Row>
            <Col span={10}>
              Distance
            </Col>
            <Col span={10}>
              Duration
            </Col>
          </Row>
          <Row>
            <Col span={10}>
              {(doc.get('distance') / 1000).toFixed(2)} km
            </Col>
            <Col span={10}>
              {Math.floor(doc.get('time') / 60)} minutes
            </Col>
          </Row>
        </Col>
        <Col span={20} offset={1}>
          <Comment
            author={doc.get('ownerDisplayName')}
            avatar={doc.get('ownerPhotoUrl')}
            content={comment}
            datetime={<Tooltip title={moment(doc.get('created').toDate()).format('YYYY-MM-DD HH:mm:ss')}>
              <span>{moment(doc.get('created').toDate()).fromNow()}</span>
            </Tooltip>}
          />
        </Col>
      </Row>);
  }

  static fromOnMapClick(triggerStateChange, context: MapStateContext, features): MapState {
    const mapState = new ViewEventMapState(triggerStateChange);
    mapState.eventId = features[0].properties.eventId;
    return mapState;
  }

  onMapClick(e: any, context: MapStateContext): MapState {
    window.history.pushState(null, null, '#/');
    return new ExploreMapState(this.triggerStateChange);
  }

  static fromUrl(eventId: any, triggerStateChange) {
    const mapState = new ViewEventMapState(triggerStateChange);
    mapState.eventId = eventId;
    return mapState;
  }
}
